.featured-article {
  position: relative;
  /* border: 1px solid black; */
}

.featured-article__image {
  min-width: 58vw;
  height: 58vw;
  border-radius: 12%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: 0 0 4px 1px #ababab;
  /* border: 1px solid black; */
}

.featured-article__info {
  width: 88%;
  border-radius: 20px;
  padding: 6% 8%;
  position: absolute;
  background-color: white;
  margin: 8% auto;
  bottom: 0;
  right: 0;
  left: 0;
  /* border: 1px solid black; */
}

.featured-article__info a {
  text-decoration: none;
}

.featured-article__title {
  font-size: 3.6vw;
  color: #222;
  font-weight: 600;
}

.featured-article__tags {
  width: 100%;
  display: inline-block;
  margin: 1vh 0 -4px 0;
}

.featured-article__tags h4 {
  font-size: 2.4vw;
  font-weight: 500;
}

.featured-article__category {
  color: #5a9bd6;
  text-transform: uppercase;
  float: left;
}

.featured-article__date {
  color: #707070;
  float: right;
}
