.sub-article {
  height: 28vw;
  width: 92vw;
  position: relative;
  /* border: 1px solid black; */
}

.sub-article__image img {
  position: absolute;
  left: 0;
  top: 6%;
  width: 30%;
  height: 88%;
  border-radius: 20%;
}

.sub-article__info {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  right: 0;
  top: 6%;
  width: 66%;
  height: 88%;
}

.sub-article__title {
  font-size: 3.6vw;
  font-weight: 600;
  color: #222;
  margin-bottom: 8px;
  font-weight: 700;
}

.sub-article__tags h4 {
  font-weight: 500;
  font-size: 2.4vw;
}

.sub-article__category {
  color: #5a9bd6;
  text-transform: uppercase;
  font-weight: 400;
  float: left;
}

.sub-article__date {
  color: #707070;
  font-weight: 500;
  float: right;
}
