@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:ital,wght@0,200;0,300;0,400;0,500;0,600;1,200;1,300;1,500&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0 auto;
  font-family: "IBM Plex Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.header-container {
  margin-top: 2vh;
  /* border: 1px solid black; */
}

.header {
  position: relative;
  width: 100vw;
  height: 34vw;
  /* border: 1px solid black; */
}

.header__logo {
  position: absolute;
  display: flex;
  height: 72%;
  left: 0;
  right: 0;
  margin: 0 auto;
  /* border: 1px solid black; */
}

.header__logo img {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  height: 100%;
  /* border: 1px solid black; */
}

.header__slogan {
  position: absolute;
  height: 16%;
  width: 100%;
  margin-top: 24.8vw;
  display: flex;
  justify-content: center;
  /* border: 1px solid black; */
}

.header__slogan h4 {
  font-weight: 300;
  font-style: italic;
  font-size: 4vw;
  color: #707070;
}

.header__current-date {
  position: absolute;
  display: flex;
  right: 0;
  bottom: 0;
  margin-right: 6vw;
}

.header__current-date h4 {
  font-size: 3.2vw;
  font-weight: 300;
  color: #707070;
}

.homescreen {
  position: relative;
  min-width: 100vw;
  border: 1px solid black;
}

.featured-articles-container {
  padding: 2vh 0 1vh 0;
  display: flex;
  overflow-x: scroll;
  -ms-scroll-snap-type: x mandatory;
      scroll-snap-type: x mandatory;
  /* border: 1px solid black; */
}

.featured-article {
  margin-left: 4vw;
  scroll-snap-align: center;
  /* border: 1px solid black; */
}

.sub-article {
  margin: 0 auto;
}

/* @media only screen and (min-width: 551px) and (max-width: 851px) {
  .sub-articles {
    display: none;
  }
} */

.featured-article {
  position: relative;
  /* border: 1px solid black; */
}

.featured-article__image {
  min-width: 58vw;
  height: 58vw;
  border-radius: 12%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: 0 0 4px 1px #ababab;
  /* border: 1px solid black; */
}

.featured-article__info {
  width: 88%;
  border-radius: 20px;
  padding: 6% 8%;
  position: absolute;
  background-color: white;
  margin: 8% auto;
  bottom: 0;
  right: 0;
  left: 0;
  /* border: 1px solid black; */
}

.featured-article__info a {
  text-decoration: none;
}

.featured-article__title {
  font-size: 3.6vw;
  color: #222;
  font-weight: 600;
}

.featured-article__tags {
  width: 100%;
  display: inline-block;
  margin: 1vh 0 -4px 0;
}

.featured-article__tags h4 {
  font-size: 2.4vw;
  font-weight: 500;
}

.featured-article__category {
  color: #5a9bd6;
  text-transform: uppercase;
  float: left;
}

.featured-article__date {
  color: #707070;
  float: right;
}
/* 
@media only screen and (min-width: 551px) and (max-width: 851px) {
  .featured-article__image {
    min-width: 40vw;
    height: 40vw;
  }
} */

.sub-article {
  height: 28vw;
  width: 92vw;
  position: relative;
  /* border: 1px solid black; */
}

.sub-article__image img {
  position: absolute;
  left: 0;
  top: 6%;
  width: 30%;
  height: 88%;
  border-radius: 20%;
}

.sub-article__info {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  right: 0;
  top: 6%;
  width: 66%;
  height: 88%;
}

.sub-article__title {
  font-size: 3.6vw;
  font-weight: 600;
  color: #222;
  margin-bottom: 8px;
  font-weight: 700;
}

.sub-article__tags h4 {
  font-weight: 500;
  font-size: 2.4vw;
}

.sub-article__category {
  color: #5a9bd6;
  text-transform: uppercase;
  font-weight: 400;
  float: left;
}

.sub-article__date {
  color: #707070;
  font-weight: 500;
  float: right;
}

