.header {
  position: relative;
  width: 100vw;
  height: 34vw;
  /* border: 1px solid black; */
}

.header__logo {
  position: absolute;
  display: flex;
  height: 72%;
  left: 0;
  right: 0;
  margin: 0 auto;
  /* border: 1px solid black; */
}

.header__logo img {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  height: 100%;
  /* border: 1px solid black; */
}

.header__slogan {
  position: absolute;
  height: 16%;
  width: 100%;
  margin-top: 24.8vw;
  display: flex;
  justify-content: center;
  /* border: 1px solid black; */
}

.header__slogan h4 {
  font-weight: 300;
  font-style: italic;
  font-size: 4vw;
  color: #707070;
}

.header__current-date {
  position: absolute;
  display: flex;
  right: 0;
  bottom: 0;
  margin-right: 6vw;
}

.header__current-date h4 {
  font-size: 3.2vw;
  font-weight: 300;
  color: #707070;
}
