.homescreen {
  position: relative;
  min-width: 100vw;
  border: 1px solid black;
}

.featured-articles-container {
  padding: 2vh 0 1vh 0;
  display: flex;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
  /* border: 1px solid black; */
}

.featured-article {
  margin-left: 4vw;
  scroll-snap-align: center;
  /* border: 1px solid black; */
}

.sub-article {
  margin: 0 auto;
}

